.footer {
  margin-top: 30px;
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  height: 100px; /* Adjust this value as needed */
  flex-direction: column; /* Stack items vertically on mobile */
  height: auto; /* Allow height to adjust based on content */
  margin-bottom: 5px;
}

.footer-social {
  display: flex;
  justify-content: center; /* Center icons horizontally */
  align-items: center; /* Center icons vertically */
}

.social-icon {
  margin: 0 4px; /* Adjust spacing between icons */
  transition: transform 1s ease-in-out, margin 0.2s ease-in-out; /* Add transition for smooth animation with slow down at start and end */
  a {
    text-decoration: none;
    color: inherit; /* Ensure the link inherits the color */
    &:hover,
    &:focus,
    &:active {
      text-decoration: none;
      background-color: none;
      color: inherit; /* Ensure the link color does not change */
      img {
        transition: transform 0.3s ease; 
        opacity: 1; /* Keep the icon fully opaque during hover */
        transform: scale(1.5); /* Increase size by 10% on hover */
        margin: 0 8px; /* Increase margin between icons on hover */
      }
    }
  }
}

.footer-text {
  font-size: 14px; /* Adjust font size as needed */
  color: var(--text-color); /* Use the same text color as defined in your root variables */
  background: rgba(255, 255, 255, 0.5);
  box-sizing: border-box;
  padding: 5px; 
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0,0,0,0.1);
  text-align: center; /* Center text horizontally */
}

