:root {
  --font-family-heading: Inter;
  --font-family-paragraph: 'Helvetica', sans-serif;
  --font-family-monospace: monospace;
  --base-color: #ededed;
  --base-offset-color: #eaeaea;
  --highlight-color: #7b16ff;
  --highlight-color2: #7b16ff;
  --heading-color: #242424;
  --text-color: #4e5157;
  --dot-color: #f0b35d;
  --dot-color2: #7b16ff;
}

$breakpoints: (
  'xsmall': 480px,
  'small': 767px,
  'medium': 992px,
  'large': 1200px,
);

@import 'normalize';
@import 'breakpoints';
@import 'header';
@import 'footer';
@import 'content';
@import 'intro';
@import 'summary';
@import 'author';
@import 'pagination';
@import 'main-menu';
@import 'main-menu-mobile';
@import 'pygments/github';

html, body {
  box-sizing: border-box;
}

body {
  background: var(--base-color);
  font-family: var(--font-family-paragraph);
  color: var(--text-color);
  margin: 0;
  padding: 20px;
  overflow-x: hidden;
  @include breakpoint('xsmall') {
    padding: 50px;
  }
  @include breakpoint('small') {
    padding: 50px;
  }
  &.frame {
    min-height: 100vh;
    border: 18px solid var(--highlight-color);
  }
}

.wrapper {
  max-width: 1000px;
  margin: 0 auto;
}
